<template>
  <div class="progress-circle">
    <!-- widthheight -->
    <!-- viewBox -->
    <svg
      :width='diameter'
      :height='diameter'
      viewBox='0 0 100 100'
      version='1.1'
      xmlns='http://www.w3.org/2000/svg'
    >
      <!--  -->
      <circle
        class='progress-background'
        r='50'
        cx='50'
        cy='50'
        fill='transparent'
      ></circle>
      <!-- -->
      <!-- stroke-dasharray
      stroke-dashoffsetdash -->
      <circle
        class='progress-bar'
        r='50'
        cx='50'
        cy='50'
        fill='transparent'
        :stroke-dasharray='dashArray'
        :stroke-dashoffset='dashOffset'
      ></circle>
    </svg>
    <!-- http://www.w3cplus.com/svg/svg-fill-stroke.html  -->
    <slot></slot>
  </div>
</template>

<script>
/*
   stroke-dasharray 
   
   stroke-dashoffset 
   */
export default {
  props: {
    diameter: {
      type: Number,
      default: 32
    },
    percent: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      dashArray: Math.PI * 100
    };
  },
  computed: {
    // percent0dashOffsetthis.dashArray
    // percent100dashOffset0
    dashOffset() {
      return (1 - this.percent) * this.dashArray;
    }
  }
};
</script>


<style scoped lang="stylus">
@import '~common/stylus/variables';

.progress-circle {
  position: relative;

  circle {
    stroke-width: 8px;
    transform-origin: center;

    &.progress-background {
      transform: scale(0.9);
      stroke: $color-theme-d;
    }

    &.progress-bar {
      transform: scale(0.9) rotate(-90deg);
      stroke: $color-theme;
    }
  }
}
</style>
